import React, { useState, useEffect } from "react";
import Sidebar from "../../../components/Admin/Sidebar";
import TopBar from "../../../components/Admin//TopBar";
import DataTable from "react-data-table-component";
import { FaTrashAlt } from "react-icons/fa";
import { FaEdit } from "react-icons/fa";
import { useNavigate } from "react-router-dom";
import { FaPlus } from "react-icons/fa6";
import { Modal, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import SuccessAlert from "../../../components/Admin/SuccessAlert";
import axios from "axios";
function AdminVideoList() {
  const [isOpen, setIsOpen] = useState(false);
  const [show, setShow] = useState(false);
  const [successMessage, setsuccessMessage] = useState("");
  const [deleteId, setdeleteId] = useState(false);
  const [showEdit, setshowEdit] = useState(false);
  const [editdata, seteditdata] = useState("");
  const [spinners, setspinnsers] = useState(false);
  const toggleNavAdmin = () => {
    setIsOpen(!isOpen); // Toggle the state between true and false
  };
  const apiUrl = "https://blueprintcatalyst.com/api/admin/video/";
  const [records, setRecords] = useState([]);

  document.title = "Video List - Admin";
  useEffect(() => {
    getvideo();
  }, []);
  const getvideo = async () => {
    let formData = {
      user_id: "",
    };
    try {
      const res = await axios.post(apiUrl + "getvideolist", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      console.log(respo);
      setRecords(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const handleEdit = async (id) => {
    setshowEdit(true);
    let formData = {
      id: id,
    };
    try {
      const res = await axios.post(apiUrl + "getvideorecord", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      console.log(respo);
      seteditdata(respo[0]);
      setMaxLimit(respo[0].max_limit);
      const fullPath = respo[0].video;
      const fileName = fullPath.split("\\").pop();
      setFile(fileName);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const columns = [
    {
      name: "Video",
      selector: (row) => row.video,
      sortable: true,
      cell: (row) => (
        <video width="100" height="70" controls>
          <source
            src={`https://blueprintcatalyst.com/api/upload/video/${row.video}`}
            type="video/mp4"
          />
          Your browser does not support the video tag.
        </video>
      ),
    },
    {
      name: "Max Limit",
      selector: (row) => row.max_limit,
      sortable: true,
      className: "age",
    },
    {
      name: "Action",
      cell: (row) => (
        <div className="d-flex align-items-center gap-3">
          <button
            type="button"
            onClick={() => handleEdit(row.id)}
            className="dataedit_btn text-success border-0 fs-5"
            title="Edit"
          >
            <FaEdit />
          </button>
          <button
            type="button"
            onClick={() => handledelete(row.id)}
            className="dataedit_btn text-danger border-0 fs-5"
            title="Delete"
          >
            <FaTrashAlt />
          </button>
        </div>
      ),
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
    },
  ];
  const handledelete = (id) => {
    setdeleteId(id);
    setShow(true);
  };
  const handleClose = () => {
    setShow(false);
    setshowEdit(false);
  };
  const [searchQuery, setSearchQuery] = useState("");

  // Define filtered data based on the search query
  const filteredRecords = records.filter((record) =>
    Object.values(record)
      .join(" ")
      .toLowerCase()
      .includes(searchQuery.toLowerCase())
  );
  const handleConfirmDelete = async () => {
    let formData = {
      id: deleteId,
    };
    try {
      const res = await axios.post(apiUrl + "videodelete", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      getvideo();
      setShow(false);
      setTimeout(() => {
        setsuccessMessage("Video deleted successfully");
      }, 1000); // 3
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
    // Close the modal after deletion
  };
  const [maxLimit, setMaxLimit] = useState("");
  const [file, setFile] = useState(null);
  const [filenew, setFilenew] = useState(null);
  const [error, setError] = useState("");
  const handleFileChange = (e) => {
    const selectedFile = e.target.files[0];
    setspinnsers(false);
    if (selectedFile) {
      const maxSize = 15 * 1024 * 1024; // 15 MB in bytes

      if (selectedFile.size > maxSize) {
        setError("File size should not exceed 15MB.");
        e.target.value = null; // clear the selected file
        setFile(null);
        setFilenew(null);
      } else {
        setError("");
        setFilenew(selectedFile);
        setFile(selectedFile);
      }
    }
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("file", filenew);
    formData.append("id", editdata.id);
    formData.append("max_limit", maxLimit);
    console.log(filenew);
    setspinnsers(true);
    try {
      if (filenew === null) {
        const res = await axios.post(apiUrl + "updatelimit", formData, {
          headers: {
            Accept: "application/json",
          },
        });
        setspinnsers(false);
        var respo = res.data.results;
        setsuccessMessage("Updated successfully");
        setTimeout(() => {
          setsuccessMessage("");
          setshowEdit(false);
          getvideo();
        }, 1000); // 3
      } else {
        const res = await axios.post(apiUrl + "updatevideo", formData, {
          headers: {
            Accept: "application/json",
          },
        });
        setspinnsers(false);
        var respo = res.data.results;
        setsuccessMessage("Updated successfully");
        setTimeout(() => {
          setsuccessMessage("");
          setshowEdit(false);
          getvideo();
        }, 1000); // 3
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  return (
    <>
      <div>
        <div className="d-flex align-items-start gap-0">
          <Sidebar />
          <div className="d-flex flex-column gap-0 w-100">
            <TopBar />
            <section className="dashboard_adminh">
              <div className="container-xl">
                <div className="row gy-4">
                  <div className="col-12">
                    <div className="card p-3">
                      <div className="d-flex justify-content-between mb-3">
                        <h5 className="mb-3">Video Management</h5>
                        <Link to="/admin/video/add" className="btn btn-primary">
                          Add <FaPlus />
                        </Link>
                      </div>

                      <div className="d-flex justify-content-end align-items-end">
                        <div class="search-bar">
                          <input
                            type="text"
                            placeholder="Search..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                          />
                          <span class="search-icon">
                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              fill="none"
                              viewBox="0 0 24 24"
                              stroke="currentColor"
                            >
                              <path
                                stroke-linecap="round"
                                stroke-linejoin="round"
                                stroke-width="2"
                                d="M21 21l-4.35-4.35M17 11a6 6 0 11-12 0 6 6 0 0112 0z"
                              />
                            </svg>
                          </span>
                        </div>
                      </div>
                      <DataTable
                        columns={columns}
                        data={filteredRecords}
                        pagination
                        highlightOnHover
                        striped
                        responsive
                        noDataComponent={
                          <div className="text-center">
                            <span>No users found</span>
                          </div>
                        }
                      />
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </div>
        </div>
      </div>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Confirm Deletion</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>Are you sure you want to delete this record?</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Cancel
          </Button>
          <Button variant="danger" onClick={handleConfirmDelete}>
            Confirm Delete
          </Button>
        </Modal.Footer>
      </Modal>

      <Modal show={showEdit} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title className="text-dark">Update</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row g-4">
            <div className="col-sm-12">
              <div className="bglight rounded h-100 p-5">
                {successMessage && (
                  <SuccessAlert
                    message={successMessage}
                    onClose={() => setsuccessMessage("")}
                  />
                )}

                <form
                  action="javascript:void(0)"
                  onSubmit={handleSubmit}
                  method="post"
                  className="d-flex flex-column gap-3"
                >
                  <div className="mb-3 text-start">
                    <label htmlFor="exampleInputFile" className="form-label">
                      Upload File <span className="text-danger">*</span>
                    </label>
                    <input type="hidden" name="id" value={editdata.id} />
                    <input
                      type="file"
                      accept="video/*"
                      required={!file}
                      name="file"
                      className="form-control"
                      id="exampleInputFile"
                      aria-describedby="fileHelp"
                      title={file}
                      onChange={handleFileChange}
                    />
                    {error && <div className="text-danger mt-1">{error}</div>}
                  </div>

                  <div className="mb-3 text-start">
                    <label htmlFor="exampleInputLimit" className="form-label">
                      Max Limit <span className="text-danger">*</span>
                    </label>
                    <input
                      type="number"
                      name="max_limit"
                      required
                      min="1"
                      className="form-control"
                      id="exampleInputLimit"
                      defaultValue={maxLimit}
                      onChange={(e) => setMaxLimit(e.target.value)}
                    />
                  </div>

                  <div className="d-grid">
                    <button
                      type="submit"
                      className="btn btn-primary position-relative"
                    >
                      {spinners && (
                        <span
                          className="spinner-border text-light position-absolute"
                          style={{
                            top: "15%",
                            left: "48%",
                          }}
                          role="status"
                        >
                          <span className="visually-hidden">Loading...</span>
                        </span>
                      )}
                      Update
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
}

export default AdminVideoList;
